<template>
  <div>
    <section class="box">
      <form @submit.prevent="validateBeforeSubmit" v-if="!alert"> 
        <b-field label="Email">
          <p class="control has-icon has-icon-right">
            <input
              name="email"
              v-model="email"
              v-validate="'required|email'"
              :class="{'input': true, 'is-danger': errors.has('email') }"
              type="text"
              placeholder="Email"
            />
            <i v-show="errors.has('email')" class="fa fa-warning"></i>
            <span v-show="errors.has('email')" class="help is-danger">Email is not valid</span>
          </p>
        </b-field>

        <b-field label="Password">
          <p class="control has-icon has-icon-right">
            <input
              name="password"
              v-model="password"
              v-validate="'required|min:8'"
              :class="{'input': true, 'is-danger': errors.has('password') }"
              type="password"
              placeholder="Enter password"
            />
            <i v-show="errors.has('password')" class="fa fa-warning"></i>
            <span
              v-show="errors.has('password')"
              class="help is-danger"
            >Password must be 8 characters at least</span>
          </p>
        </b-field>

        <b-field
          label="Confirm password"
          :type="{'is-danger': errors.has('confirm-password')}"
          :message="[{
                    'You must confirm password' : errors.firstByRule('confirm-password', 'required'),
                    'Passwords are not the same'  : errors.firstByRule('confirm-password', 'is')
                }]"
        >
          <b-input
            type="password"
            placeholder="Enter password again"
            v-model="confirmPassword"
            name="confirm-password"
            v-validate="{ required: true, is: password }"
          />
        </b-field>

        <button type="submit" class="button is-primary">Register</button>
      </form>

      <div v-if="alert" class="has-text-centered">
        <p><i class="far fa-grin-beam fa-2x"></i>You will be redirected to the payment page !</p>
        <p>This is a demo version</p>
        <p>YOU WILL NOT BE DEBITED</p>
        <br>
        <p>YOU MUST ENTER "4242 4242 4242 4242" AS CARD NUMBER</p>
        <p>It will simulate a real payment</p>
        <button class="button is-warning" @click="createStripe">OK</button>
      </div>


    </section>
  </div>
</template>

<script>
import axiosRequest from "../../axiosRequests";
export default {
  data() {
    return {
      email: null,
      password: null,
      confirmPassword: null,
      alert : false,
    };
  },
  created() {
    if (this.$cookies.isKey("user")) {
      this.alert = true;
    }
  },
  methods: {
    //CREATE STRIPE CUSTOMER AND SUBSCRIPTION
    createStripe() {
      const stripe = Stripe("pk_test_oevDNgnDf7MryOlZOKGdT3AV");
      stripe
        .redirectToCheckout({
          items: [
            // Replace with the ID of your SKU
            { plan: "plan_FaLwspsa2HreoF", quantity: 1 }
          ],
          successUrl: "https://stopover-chat.herokuapp.com/success",
          cancelUrl: "https://stopover-chat.herokuapp.com/error",
          customerEmail: this.$store.getters.userEmail
        })
        .then(function(result) {
          if (result) {
            this.displaySuccess("You succesfully registered");
          } else {
            this.displayError("We are not able to register you");
          }
        });
    },
    validateBeforeSubmit() {
      this.$validator.validateAll().then(result => {
        if (result) {
          const formData = {
            email: this.email,
            password: this.confirmPassword
          };
          axiosRequest
            .createUser(formData)
            .then((response) => {
              this.displaySuccess(response.success)
              this.$store.state.user = response.user
              this.$store.state.isConnected = true;
              this.alert = true;
            })
            .catch(error => {
              this.displayError(error);
              this.email = this.password = this.confirmPassword = null;
            });

          return;
        }
        this.$buefy.toast.open({
          message: "Form is not valid !",
          type: "is-danger",
          position: "is-bottom"
        });
      });
    },
    displayError(error) {
      this.$buefy.toast.open({
        message: error,
        type: "is-danger",
        position: "is-bottom",
        duration: 5000
      });
    },
    displaySuccess(response) {
      this.$buefy.toast.open({
        message: response,
        type: "is-success",
        position: "is-bottom",
        duration: 5000
      });
    },
  }
};
</script>

<style scoped>
section {
  width: 350px;
  margin: auto;
  margin-top: 50px;
}
.fa-grin-beam {
  color : #FFDA4A;
  padding-right: 10px;
}
</style>
